<template>
  <div class="home">
    <el-container>
      <el-aside  :width="isCollapse ? '64px' : '200px'">
        <div class="side-top">
          <el-button style="color:aliceblue" v-if="!isCollapse" icon="el-icon-s-fold" @click="exchangeNav" ></el-button>
          <el-button style="color:aliceblue" v-else icon="el-icon-s-unfold" @click="exchangeNav"></el-button>
        </div>
        <div class="side-middle">
          <div v-for="(item,index) in navItems" v-show="item.isDisplay" :class="{active:item.isactive}" @click="rouerTo(item.url,index)" :key="index" class="nav-items">
          <div><i :class="item.icon"></i>
          <span v-if="!isCollapse">{{item.title}}</span></div>
          </div>
        </div>
        <div class="side-bottom">
          <div v-for="(item,index) in navItemsBottom" @click="rouerTo(item.url,index)" :key="index" class="nav-items">
          <i :class="item.icon"></i>
          <span v-if="!isCollapse">{{item.title}}</span>
        </div>
        </div>
      </el-aside>
      <el-main>
        <keep-alive>
        <router-view>
        </router-view>
        </keep-alive>
      </el-main>
      <Dialog  title="退出确认" @cancel="logOutDialogDisplay=false" @confirm="confirmClick" description="确定要退出吗？" :isDisplay.sync="logOutDialogDisplay"></Dialog>
    </el-container>
  </div>
</template>

<script>
import Dialog from "components/Dialog.vue"
export default {
  name: "Home",
  components: {
    Dialog
  },
  mixins: [],
  props: {
    msg: {
      type: String,
      default: "Home",
    },
  },
  data: function () {
    return {
      isCollapse:false,
      logOutDialogDisplay:false,
      navItems:[
        {
          title:'工作流程',
          url:'/workflow',
          icon:'el-icon-guide',
          isactive:false,
          isDisplay:true,
        },
        /* {
          title:'参数配置',
          url:'/work',
          icon:'el-icon-menu'
        }, */
        {
          title:'权限管理',
          url:'/authority',
          icon:'el-icon-s-check',
          isactive:false,
          isDisplay:true,
        },
        {
          title:'拓展更多',
          url:'/expansion',
          icon:'el-icon-plus',
          isactive:false,
          isDisplay:true,
        },
        {
          title:'报告查询',
          url:'/query',
          icon:'el-icon-s-order',
          isactive:false,
          isDisplay:true,
        }
      ],
      navItemsBottom:[
        {
          title:'主页',
          url:'/home',
          icon:'el-icon-s-home'
        },
        {
          title:'退出',
          url:'/logout',
          icon:'el-icon-switch-button'
        }
      ],
      id:''
    };
  },
  computed: {},
  watch: {},
  created() {
    console.log("home被加载;");
  },
  mounted() {
    const id = this.$route.query.id
    if(id==="2"){
      this.navItems[1].isDisplay = false;
    }else if(id==="3"){
      this.navItems[1].isDisplay = false;
      this.navItems[2].isDisplay = false;
    }
    this.id=id;
  },
  methods: {
    exchangeNav(){
      this.isCollapse = !this.isCollapse;
    },
    rouerTo(url,selectIndex){
      if(url==='/logout'){
        this.logOutDialogDisplay = true;
        return
      }
      if(url==='/home'){
        console.log("转到home")
        this.$router.push({path:'/home',query: {id:this.id}});
      }else{
        this.$router.push(url);
      }
      this.navItems.map((item,index)=>{
        if(index==selectIndex){
          item.isactive = true;
        }else{
          item.isactive = false;
        }
      })
    },
      confirmClick(){
          this.$router.push('./login')
      }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.home {
  height: 100%;
}
.el-container{
  height: 100%;
}
.el-aside{
  height: 100%;
  background-color: #5394FF;
  color:aliceblue;
}
.side-top{
  text-align: left;
  margin-bottom: 80px;
}
.el-aside .el-button{
  background-color: #5394FF;
  border:none;
  font-size:24px;
}
.toggle-button{
  background-color: #5394FF;
  border-right:1px;
}
.side-middle{
  text-align:left;
}
.nav-items{
  display:flex;
  align-items:center;
  padding-bottom: 12px;
  padding-top:12px;
  font-size:24px;
  padding-left:20px;
}
.nav-items span{
  margin-left:30px;
  font-size:16px;
}
.side-bottom{
  position: absolute;
  bottom:20px;
}
.active{
  background-color: #5677FC;
  box-shadow: 0 2px 12px 2px rgb(0 0 0 / 50%);
}
</style>
